/* imports for core function */
import Vue from 'vue'
import App from './App.vue'
//import router from './router'
import Router from 'vue-router'

/* import for function */
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import Parallax from 'vue-parallaxy' //parallax effect

import AOS from 'aos'                // Animate on scroll
import 'aos/dist/aos.css';

import VueAwesomeSwiper from 'vue-awesome-swiper'   //photo or pannel swiper
import 'swiper/dist/css/swiper.css'

import BackToTop from 'vue-backtotop' //Back to top plugin

import VueI18n from 'vue-i18n' //internationalization

// import { animateOnScroll } from "mdbvue";
// import VueScrollmagic from 'vue-scrollmagic' // for advance animation on phone; act or more advance than skrollr
// import scrollmagic from 'scrollmagic'

/* imports component/view */
//import index from './views/index.vue'
import testIndex from './views/testIndex.vue'
// import test from './views/test.vue'


// plug-ins use
Vue.use(Router);
Vue.use(BootstrapVue); // scrollspy component | cause bootstrap scrollspy not working when 100vh/100% is used.

Vue.use(Parallax);
Vue.use(VueAwesomeSwiper);
Vue.use(BackToTop);

Vue.use(VueI18n);
// Vue.use(animateOnScroll)
// Vue.use(VueScrollmagic)
// Vue.use(scrollmagic)

// Vue.use(AOS.init()) //for self-testing later (self-understanding) -> it activate "too Early" upon actions in browser/DOM

Vue.config.productionTip = false

// define router
const router = new Router({
  routes:[
    {path:'/', component:testIndex},
    {path:'/Home', component:testIndex, template:'id=#Home'},
    {path:'/About', component:testIndex, template:'id=#About'},
    {path:'/Features', component:testIndex, template:'id=#Features'},
    {path:'/Screenshots', component:testIndex, template:'id=#Screenshots'},
    {path:'/Download',component:testIndex, template:'id=#Download'}
  ]
})

const messages = {
  日本語: require('./components/locales/jp.json'),
  English: require('./components/locales/en.json'),
}

const i18n = new VueI18n({
  messages,  
  locale:'English'
})


new Vue({
  created(){
    AOS.init() // could work as well. - Activate after DOM or upon action.
  },
  i18n,
  router,
  render: h => h(App)
}).$mount('#app')
