                <p class="text-copyright">COPYRIGHT &copy; 2019. ALL RIGHTS RESERVED.</p>
              </div>
            </div>
          </b-col>
        </b-row>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //language data tracking
      langs: ["日本語", "English"],
      language: this.$t("language"),

      swiperOption: {
        slidesPerView: 2,
        spaceBetween: 50,
        // init: false,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        breakpoints: {
          1200: {
            slidesPerView: 2,
            spaceBetween: 200
          },
          992: {
            slidesPerView: 2,
            spaceBetween: 200
          },
          768: {
            slidesPerView: 1,
            spaceBetween: 200
          },
          576: {
            slidesPerView: 1,
            spaceBetween: 100
          }
        }
      }
    };
  },
  // Toggling visibility of b-navbar background
  methods: {
    isScrollNavBar() {
      if (window.scroll) {
        document
          .getElementById("topMenuItem")
          .classList.replace("bgtransparent", "bgNavbarColor");
      }
      if (window.scrollY === 0) {
        document
          .getElementById("topMenuItem")
          .classList.replace("bgNavbarColor", "bgtransparent");
      }
    },
    //switch language
    switch_language(lang) {
      localStorage.setItem("lang", lang);
      this.$i18n.locale = lang;

      //set to tracking data object
      this.language = lang;
      if (lang == "日本語") {
        var post_lang_jp = "jp";
        localStorage.setItem("post_lang", post_lang_jp);
      } else if (lang == "English") {
        var post_lang_en = "en";
        localStorage.setItem("post_lang", post_lang_en);
      }
    }
  },
  created: function() {
    // user langauge setting info retrival.
    if (localStorage.getItem("lang") !== null) {
      this.$i18n.locale = localStorage.getItem("lang");
      this.language = localStorage.getItem("lang");
    }
  },
  mounted: function() {
    window.addEventListener("scroll", this.isScrollNavBar);
  },
  destroyed() {
    window.removeEventListener("scroll", this.isScrollNavBar);
  }
};
</script>

